              <el-input placeholder="请填写整数或小数" class="header-right-time right" :clearable='true' v-model="infoDataObj.contributeSize"></el-input>
              <span class="inputbox-error" v-if="contributeSize">{{contributeSizeErrormsg}}</span>
            </div>
            <div class="step1-select-box select-normal">
              <span><span class="account-icon icon-required"></span>实缴规模(万元)</span>
              <el-input placeholder="请填写整数或小数" class="header-right-time right" :clearable='true' v-model="infoDataObj.actualSize"></el-input>
