          <div>
            <div class="step1-select-box select-normal fundManageDetail_qx ">
              <span> &nbsp;&nbsp;&nbsp;&nbsp;存续期限(年)</span>
              <el-input class='right' :clearable='true' v-model="qx_3"></el-input><span class='right'>&nbsp;+&nbsp;</span>
              <el-input class='right' :clearable='true' v-model="qx_2"></el-input><span class='right'>&nbsp;+&nbsp;</span>
