        </div>
        <div class="clear step2-box">
          <div class="step-title step2-title">Part 2：已投项目</div>
          <!-- 数据卡片 -->
          <div class="account-itembox" v-for="(item,index) of cast_company" :key="index" @click="changeFund(item,index,1)">
            <div class="account-item">
