              <div class="account-item-infobox clear">
                <div class="account-item-infobox-item">
                  <span class="account-icon  icon-time"></span>
                  <p class="text">投资时间：{{item.invest_time}}</p>
                </div>
              </div>
              <div class="account-item-infobox clear">
                <div class="account-item-infobox-item">
                  <span class="account-icon icon-money "></span>
                  <p class="text">投资金额（万元）：{{item.had_invest!=''&&item.had_invest!=undefined?item.had_invest:'未知'}}</p>
                </div>
              </div>
              <div class="account-item-btnbox clear">
                <div class="account-item-btn account-item-btn-delete" @click.stop="deleteFund(item.id,index,1)">
                  <span class="account-icon icon-delete"></span> 删除项目
                </div>
