            </div>
          </div>
          <!-- 添加账号卡片-->
          <div class="account-itembox">
            <div class="account-item account-additem">
              <span class="account-icon icon-addaccount" @click="createDialogShow=true;alertType=1;isEditor=false"></span> 添加已投项目
            </div>
          </div>
        </div>
        <div class="clear step2-box">
          <div class="step-title step2-title">Part 3：退出项目</div>
          <!-- 数据卡片 -->
          <div class="account-itembox" v-for="(item,index) of out_company" :key="index" @click="changeFund(item,index,2)">
            <div class="account-item">
              <div class="account-item-name">
                {{item.company_name}}
              </div>
              <div class="account-item-infobox clear">
                <div class="account-item-infobox-item">
                  <span class="account-icon  icon-time"></span>
                  <p class="text">投资时间：{{item.invest_time}}</p>
                </div>
              </div>
              <div class="account-item-infobox clear">
                <div class="account-item-infobox-item">
                  <span class="account-icon icon-money "></span>
                  <p class="text">投资金额（万元）：{{item.had_invest!=''&&item.had_invest!=undefined?item.had_invest:'未知'}}</p>
                </div>
              </div>
              <div class="account-item-btnbox clear">
                <div class="account-item-btn account-item-btn-delete" @click.stop="deleteFund(item.id,index,2)">
                  <span class="account-icon icon-delete"></span> 删除项目
                </div>
              </div>
            </div>
          </div>
          <!-- 添加账号卡片-->
          <div class="account-itembox">
            <div class="account-item account-additem">
              <span class="account-icon icon-addaccount" @click="createDialogShow=true;alertType=2;isEditor=false"></span> 添加已退出项目
            </div>
          </div>
        </div>
        <div class="step3-box">
          <button class="btn-blue" @click="addOrEditorInvestor">提交</button>
          <!-- <button class="btn-blue" v-else @click="editInvestor">提交</button> -->
        </div>
      </div>
      <!-- 弹窗 -->
      <!-- :title='alertType==1?"已投项目":"退出项目"' -->
      <el-dialog title='基金项目信息添加' :visible.sync="createDialogShow" width="570px" center :append-to-body='true' :before-close="toCancleConfirm">
        <div class="dialog_content">
