                  <!-- 当是已投时显示的项目列表 -->
                  <el-select placeholder="请选择" v-model="alertDataObj.company_id" filterable :clearable='true' @change="dataChange" v-if="alertType==1">
                    <el-option v-for="item of alreadyProjectList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
                    </el-option>
                  </el-select>
                  <!-- 当是退出时显示的项目列表 -->
                  <el-select placeholder="请选择" v-model="alertDataObj.company_id" filterable :clearable='true' @change="dataChange" v-if="alertType==2">
                    <el-option v-for="item of leaveProjectList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
                    </el-option>
                  </el-select>
                </div>
                <span class="form-group-inputbox-error" v-if="fund">{{fundErrormsg}}</span>
