            </div>
            <div class="form-group-inputboxgroup clear ">
              <div class="form-group-inputbox">
                <h2 class="form-group-inputbox-title"><span class="account-icon icon-required"></span>投资时间</h2>
                <div class="form-group-inputbox-selectbox select-normal-40 left">
                  <el-date-picker type="date" placeholder="选择时间" v-model="alertDataObj.invest_time" :editable='false' :clearable='true' value-format="yyyy-MM-dd" class="header-right-time right" @change="dataChange">
                  </el-date-picker>
                </div>
                <span class="form-group-inputbox-error" v-if="investTime">{{investTimeErrormsg}}</span>
              </div>
            </div>
          </div>
          <div class="form-group-inputboxgroup clear margin-bottom-none">
            <div class="form-group-inputbox">
              <h2 class="form-group-inputbox-title">投资金额(万元)</h2>
              <div class="form-group-inputbox-selectbox select-normal-40 left">
                <el-input v-model="alertDataObj.had_invest" placeholder="请填写" :clearable='true' @change="dataChange" class="header-right-time right"></el-input>
              </div>
              <span class="form-group-inputbox-error" v-if="hadInvest">{{hadInvestErrormsg}}</span>
            </div>
          </div>
        </div>
        <div slot="footer" class="dialog_footer clear">
          <button class="dialog-btn-submit btn-blue" @click="alertConfirmEvent">确定</button>
          <button class="dialog-btn-submit btn-blue-border" @click="toCancleConfirm">取消</button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
import {
  showComment,
  addOrEditFund,
  showEditFund,
  fundExcel2,
  exportExcel,
  downloadExcel2
} from '../../config/api'
import {
  dialogConfirm,
  dialogConfirmHasbtn
} from '../../config/mUtils'
import api from '../../config/url'; //引用url.js
export default {
  data() {
    return {
      path: {
        from: '/home/fundManage',
        name: '基金数据'
      },
      createDialogShow: false, //新增账户弹窗显示\
      isset: 0,
      infoDataObj: {
        fundName: '', //基金名称
        status: '', //募集状态
        type: '', //基金状态
        regTime: '', //成立时间
        duration: '', //存续期限
        contributeSize: '', //认缴规模
        actualSize: '', //实缴规模
        direction: '', //投资方向
        company_arr: [], //
      }, //存放基本信息的内容
      infoDataArr: [], //提交时存放已投和退出项目信息的数组
      cast_company: [], //存放已投项目信息的数组
      out_company: [], //存放已退出项目信息的数组
      alertDataObj: {}, //存放弹窗里面基金信息
      alertIndex: null, //修改时展示的信息在表格数组的下标
      //基本信息错误信息
      regTimeErrormsg: '',
      statusErrormsg: '',
      typeErrormsg: '',
      contributeSizeErrormsg: '',
      actualSizeErrormsg: '',
      durationErrormsg: '',
      fundNameErrormsg: '',
      //弹出框错误信息
      fundErrormsg: '',
      hadInvestErrormsg: '',
      investTimeErrormsg: '',

      notConfim: true,
      notAlertConfim: true,
      isEditor: false,
      canSave: false,
      showUploadBtn: false, //显示上传文件的按钮
      //新增
      fundName1: null,
      alreadyProjectList: [], //已投的项目列表
      leaveProjectList: [], //退出的项目列表
      alertType: null, //弹出框的类型 1是已投，2是退出
      statusList: [{
        id: 1,
        name: '在售'
      }, {
        id: 0,
        name: '售罄'
      }],
      typeList: [{
        id: 1,
        name: '储备'
      }, {
        id: 2,
        name: '存续'
      }, {
        id: 3,
        name: '退出'
      }],
      qx_1: '',
      qx_2: '',
      qx_3: '',
      page: 1,
    }
  },
  created() {
    if (this.$route.query.path != undefined && this.$route.query.path != '') {
      this.path = this.$route.query.path
    }
    if (this.$route.query.page) {
      this.page = this.$route.query.page;
    }
    //获取弹窗下拉项目下拉列表
    this.getProjectList(1);
    this.getProjectList(2);
    if (this.$route.query.id != undefined) { //编辑
      this.fundName1 = decodeURIComponent(this.$route.query.name);
      //获取
      this.getInvestorInfo(this.$route.query.id)
      this.showUploadBtn = true;
    } else { //新增
      this.fundName1 = '添加新基金'
      this.showUploadBtn = false;
    }
  },
  mounted() {

  },
  computed: {
    //基本信息验证
    fundName: function() { //基金名称
      if (this.notConfim) {
        return false
      }
      if (this.infoDataObj.fundName == '' || this.infoDataObj.fundName == undefined) {
        this.fundNameErrormsg = "*基金名称不能为空";
        return true;
      } else {
        return false;
      }
    },
    regTime: function() { //成立时间
      if (this.notConfim) {
        return false
      }
      if (this.infoDataObj.regTime == '' || this.infoDataObj.regTime == undefined) {
        this.regTimeErrormsg = "*成立时间不能为空";
        return true;
      } else {
        return false;
      }
    },
    // status: function() { //募集状态
    //   if (this.notConfim) {
    //     return false
    //   }
    //   if (this.infoDataObj.status == 0) {
    //     return false;
    //   } else {
    //     if (this.infoDataObj.status == '' || this.infoDataObj.status == undefined) {
    //       this.statusErrormsg = "*募集状态不能为空";
    //       return true;
    //     } else {
    //       return false;
    //     }
    //   }

    // },
    // type: function() { //募集状态
    //   if (this.notConfim) {
    //     return false
    //   }
    //   if (this.infoDataObj.type == '' || this.infoDataObj.type == undefined) {
    //     this.typeErrormsg = "*基金状态不能为空";
    //     return true;
    //   } else {
    //     return false;
    //   }
    // },
    contributeSize: function() { //认缴规模
      if (this.notConfim) {
        return false
      }
      let contributeSizeTest = /^[+]{0,1}(\d+)$|^[+]{0,1}(\d+\.\d+)$/;
      if (this.infoDataObj.contributeSize === '' || this.infoDataObj.contributeSize == undefined) {
        this.contributeSizeErrormsg = "*认缴规模不能为空"
        return true;
      } else {
        if (!contributeSizeTest.test(this.infoDataObj.contributeSize)) {
          this.contributeSizeErrormsg = "*认缴规模格式不正确"
          return true;
        } else {
          return false;
        }
      }
    },
    actualSize: function() { //实缴规模
      if (this.notConfim) {
        return false
      }
      let actualSizeTest = /^[+]{0,1}(\d+)$|^[+]{0,1}(\d+\.\d+)$/;
      if (this.infoDataObj.actualSize == '' || this.infoDataObj.actualSize == undefined) {
        this.actualSizeErrormsg = "*实缴规模不能为空"
        return true;
      } else {
        if (!actualSizeTest.test(this.infoDataObj.actualSize)) {
          this.actualSizeErrormsg = "*实缴规模格式不正确"
          return true;
        } else {
          return false;
        }
      }
    },
    duration: function() { //存续期限
      if (this.notConfim) {
        return false
      }
      let numberTest = /^([1-9]\d*|[0]{1,1})$/;
      if (this.qx_1 == '' && this.qx_2 == '' && this.qx_3 == '') {
        this.infoDataObj.duration = '';
        return false;
      } else {
        if (this.qx_1 != '') {
          if (this.qx_2 == '') {
            this.durationErrormsg = "*存续期限前两项不能为空";
            return true;
          }
        }
        if (this.qx_2 != '') {
          if (this.qx_1 == '') {
            this.durationErrormsg = "*存续期限前两项不能为空";
            return true;
          }
        }
        if (this.qx_3 != '') {
          if (this.qx_1 == '' || this.qx_2 == '') {
            this.durationErrormsg = "*存续期限前两项不能为空";
            return true;
          }
        }
        if (numberTest.test(this.qx_1) && numberTest.test(this.qx_2)) {
          if (this.qx_3 != '') {
            if (numberTest.test(this.qx_3)) {
              this.infoDataObj.duration = this.qx_1 + '+' + this.qx_2 + '+' + this.qx_3;
              return false;
            } else {
              this.durationErrormsg = "*存续期限只能为整数";
              return true;
            }
          } else {
            this.infoDataObj.duration = this.qx_1 + '+' + this.qx_2;
            return false
          }
        } else {
          this.durationErrormsg = "*存续期限只能为整数";
          return true;
        }
      }
    },

    //弹窗信息验证
    fund: function() { //项目
      if (this.notAlertConfim) {
        return false
      }
      if (this.alertDataObj.company_id == '' || this.alertDataObj.company_id == undefined) {
        this.fundErrormsg = "*请选择基金";
        return true;
      } else {
        return false;
      }
    },
    hadInvest: function() { //投资金额
      if (this.notAlertConfim) {
        return false
      }
      let hadInvestTest = /^[+]{0,1}(\d+)$|^[+]{0,1}(\d+\.\d+)$/;
      if (this.alertDataObj.had_invest == '' || this.alertDataObj.had_invest == undefined) {
        return false;
      } else if (!hadInvestTest.test(this.alertDataObj.had_invest)) {
        this.hadInvestErrormsg = "*投资金额格式不正确"
        return true;
      } else {
        return false;
      }
    },
    investTime: function() { //投资时间
      if (this.notAlertConfim) {
        return false
      }
      if (this.alertDataObj.invest_time == '' || this.alertDataObj.invest_time == undefined) {
        this.investTimeErrormsg = "*投资时间不能为空"
        return true;
      } else {
        return false;
      }
    },

  },
  methods: {
    //获取项目下拉列表的数据
    getProjectList(status) {
      showComment({
        status: status,
      }).then(res => {
        if (res.data.msgNo == 1) {
          if (status == 1) {
            this.alreadyProjectList = res.data.payload;
          } else {
            this.leaveProjectList = res.data.payload;
          }

        }
      });
    },
    //获取投资人信息详情
    getInvestorInfo(id) {

      showEditFund({
        fund_id: id
      }).then(res => {
        if (res.data.msgNo == 1) {
          let data = res.data.payload;
          this.infoDataObj.fundName = data.fundName;
          this.infoDataObj.status = data.status;
          this.infoDataObj.type = data.type;
          this.infoDataObj.regTime = data.regTime;
          this.infoDataObj.actualSize = data.actualSize;
          this.infoDataObj.contributeSize = data.contributeSize;
          this.infoDataObj.direction = data.direction;
          this.isset = data.isset;
          this.cast_company = data.cast_company;
          this.out_company = data.out_company;
          if (data.duration == null || data.duration == undefined) {
            data.duration = '';
          }
          if (data.duration != '') {
            data.duration = data.duration.toString();
            let durationArr = data.duration.split('+');
            if (durationArr.length == 1) {
              this.qx_1 = durationArr[0];
            } else if (durationArr.length == 2) {
              this.qx_1 = durationArr[0];
              this.qx_2 = durationArr[1];
            } else if (durationArr.length == 3) {
              this.qx_1 = durationArr[0];
              this.qx_2 = durationArr[1];
              this.qx_3 = durationArr[2];
            }
          }
        }
      })
    },
    //添加投资人信息
    addOrEditorInvestor() {
      this.notConfim = false;
      // if (!this.regTime && !this.status && !this.type && !this.contributeSize && !this.actualSize && !this.duration && !this.fundName) {
      if (!this.regTime && !this.contributeSize && !this.actualSize && !this.duration && !this.fundName) {
        this.infoDataObj.company_arr = [];
        //将已投项目放入数组
        if (this.cast_company.length != 0) {
        	  var totleHavePost = 0;
          this.cast_company.forEach(item => {
          	console.log(item);
            this.infoDataObj.company_arr.push(item);
            totleHavePost = totleHavePost + parseFloat(item.had_invest);
          });
        }
        //将未投项目放入数组
        if (this.out_company.length != 0) {
          this.out_company.forEach(item => {
            this.infoDataObj.company_arr.push(item);
          });
        }
        // 如果未添加项目信息 则传空值
        if (this.infoDataObj.company_arr.length == 0) {
          this.infoDataObj.company_arr = ''
        } else {
          this.infoDataObj.company_arr.forEach(item => {
            if (item.had_invest == '' || item.had_invest == null || item.had_invest == undefined) {
              delete item.had_invest;
            }
          });
        }
        //如果是编辑传基金id
        if (this.$route.query.id != undefined) {
          this.infoDataObj.fund_id = this.$route.query.id;
        }
        if (totleHavePost == undefined ||  totleHavePost<= this.infoDataObj.actualSize) {
			addOrEditFund(this.infoDataObj).then(res => {
	          if (res.data.msgNo == 1) {
	            dialogConfirm('success', '添加成功');
	            this.$router.push({
	              path: this.path.from,
	            })
	          } else {
	            dialogConfirm('info', res.data.message);
	          }
	        })
        } else{
            dialogConfirm('info', '已投资金额应小于实缴规模');
        }
      }
    },

    //修改卡片（已投和退出）
    changeFund(item, index, type) {
      this.alertType = type;
      this.alertIndex = index;
      this.isEditor = true;
      this.alertDataObj = JSON.parse(JSON.stringify(item));
      this.createDialogShow = true;
    },
    //删除卡片（已投和退出）
    deleteFund(id, index, type) {
      let dataArr = [];
      if (type == 1) {
        dataArr = this.cast_company;
      } else if (type == 2) {
        dataArr = this.out_company;
      }
      dialogConfirmHasbtn('确定删除该项目？').then(() => {
        dataArr.splice(index, 1);
      }).catch(() => {});
    },
    //弹出框确定按钮
    alertConfirmEvent() {
      this.notAlertConfim = false;
      let dataArr = [];
      let companyList = [];
      if (this.alertType == 1) {
        dataArr = this.cast_company;
        companyList = this.alreadyProjectList;
      } else if (this.alertType == 2) {
        dataArr = this.out_company;
        companyList = this.leaveProjectList;
      }
      if (!this.fund && !this.hadInvest && !this.investTime) {
        if (dataArr.length > 0) {
          let number = 0;
          for (let item1 of dataArr) {
            if (item1.company_id == this.alertDataObj.company_id) {
              if (this.isEditor) {
                if (number != this.alertIndex) {
                  dialogConfirm('info', '项目已经存在');
                  return false;
                }
              } else {
                dialogConfirm('info', '项目已经存在');
                return false;
              }
            }
            number++;
          }
        }
        for (let item of companyList) {
          if (item.company_id == this.alertDataObj.company_id) {
            this.alertDataObj.company_name = item.company_name
          }
        }
        if (this.isEditor) {
          dataArr[this.alertIndex] = this.alertDataObj;
          this.isEditor = false;
        } else {
          dataArr.push(this.alertDataObj);
        }
        this.createDialogShow = false;
        this.canSave = false;
        this.notAlertConfim = true;
        this.alertDataObj = {};
      }
    },
    //弹窗内容发生改变时调用
    dataChange() {
      this.canSave = true;
    },
    //取消提交
    toCancleConfirm() {
      if (this.canSave) {
        dialogConfirmHasbtn('确定不提交并关闭么？').then(() => {
          this.canSave = false;
          this.createDialogShow = false;
          this.alertDataObj = {};
          this.notAlertConfim = true;
        }).catch(() => {});
      } else {
        this.createDialogShow = false;
        this.alertDataObj = {};
        this.notAlertConfim = true;
      }
    },

    //导入Excel文件
    uploadFundExcel(e) {
      let inputDOM = this.$refs.inputer;
      // 通过DOM取文件数据
      let file = inputDOM.files[0];
      if (file == undefined) {
        return false;
      }
      if (!/\.xl(s[xmb]|t[xm]|am|s)$/.test(file.name)) {
        dialogConfirm('info', '请选择Excel格式文件')
        inputDOM.value = '';
        return false
      }
      fundExcel2({
        fundId: this.$route.query.id
      }, file).then(res => {
        if (res.data.msgNo == 1) {
          dialogConfirm('success', '导入成功');
          this.getProjectList(1);
          this.getProjectList(2);
          this.getInvestorInfo(this.$route.query.id)
          this.isset = 1;
          inputDOM.value = '';
        } else {
          dialogConfirm('info', res.data.message);
          inputDOM.value = '';
        }
      })
    },
    //导出Excel文件
    toDownloadExcel() {
      // window.location.href = api.url + '/web/fund/exportExcel?projId=' + this.$route.query.id;
      let table_token =   window.localStorage.getItem("htcx_server_table_token");
      window.location.href = api.url + '/web/fund/exportExcel?projId=' + this.$route.query.id+'&table_token='+table_token;
    },
    //下载基金模版
    downloadFundFile() {
      downloadExcel2().then(res => {
        if (res.data.msgNo == 1) {
          window.location.href = res.data.payload;
        } else {
          dialogConfirm('info', res.data.message)
        }
      });
    },
  }
};

</script>
